body {
  margin: 0;
  padding: 0;
  font-family: 'Nunito', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #707070;
  color: #D8D8D8;
}



.title{
  font-size: 70px;
  text-align: center;
}
.button{
  font-size: 110px;
  margin-right: 20px;
  margin-left: 20px;
  font-weight: 500;
}


.button :hover{
  color: #707070;
  transition: 0.5s;
  cursor: pointer;
}

@media screen and (max-width: 769px) {
  .title{
    font-size: 40px;
    text-align: center;
  }
  .button{
    font-size: 40px;
    font-weight: 500;
  }
  
}



.options{
  display: flex;
  align-self: stretch;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
}
* {
    box-sizing: border-box;
  }
  

  
  .field {
    margin-left: 10px;
    margin-right: 10px;
    width: 150px;
    height: 56px;
    border-radius: 4px;
    border-color: transparent;
    position: relative;
    background-color: rgba(255, 255, 255, 0.2);
    transition: 0.3s background-color ease-in-out, 0.3s box-shadow ease-in-out;
  }
  
  .field:hover {
    background-color: rgba(255, 255, 255, 0.45);
    box-shadow: 0px 4px 20px 0px rgba(0, 0, 0, 0.05);
  }
  
  .field.active {
    background-color: rgba(255, 255, 255, 0.8);
        outline-width: 0;

    box-shadow: 0px 4px 20px 0px rgba(0, 0, 0, 0.2);
  }
  
  .field.active input {
    padding: 24px 16px 8px 16px;
  }
  
  .field.active input + label {
    top: 4px;
    opacity: 1;
    color: #512da8;
  }
  
  .field.locked {
    pointer-events: none;
  }
  
  .field input {
    width: 100%;
    height: 56px;
    position: relative;
    padding: 0px 16px;
    border: none;
    border-radius: 4px;
    font-family: "Gotham SSm A", "Gotham SSm B", sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: normal;
    background-color: transparent;
    color: #282828;
    outline: none;
    box-shadow: 0px 4px 20px 0px transparent;
    transition: 0.3s background-color ease-in-out, 0.3s box-shadow ease-in-out,
      0.1s padding ease-in-out;
    -webkit-appearance: none;
  }
  
  .field input::-webkit-input-placeholder {
    color: rgba(255, 255, 255, 0.8);
  }
  .field input::-moz-placeholder {
    color: rgba(255, 255, 255, 0.8);
  }
  .field input:-ms-input-placeholder {
    color: rgba(255, 255, 255, 0.8);
  }
  .field input:-moz-placeholder {
    color: rgba(255, 255, 255, 0.8);
  }
  
  .field input + label {
    position: absolute;
    top: 24px;
    left: 16px;
    font-family: "Gotham SSm A", "Gotham SSm B", sans-serif;
    font-size: 12px;
    font-weight: 600;
    line-height: 24px;
    color: #ffffff;
    opacity: 0;
    pointer-events: none;
    transition: 0.1s all ease-in-out;
  }
  
  .field input + label.error {
    color: #ec392f;
  }
  
  .field p.predicted {
    position: absolute;
    top: 8px;
    left: 16px;
    font-family: "Gotham SSm A", "Gotham SSm B", sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    color: #e0e0e0;
    opacity: 1;
    pointer-events: none;
  }
  
.infoTitle{
    font-size: 70px;
    color: #e0e0e0;
    text-align: center
}
@media screen and (max-width: 769px) {
  .infoTitle{
    font-size: 40px;
    color: #e0e0e0;
    text-align: center;
  }
}

.options{
  display: flex;
  align-self: stretch;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
